import Vue from 'vue'
import pageTranslator from '@nnm/page-translator'; Vue.use(pageTranslator);
import axios from 'axios';
import Rendezvous from "../modals/ajout_rendezvous";
  export default {
    name:"Calendrier",
     created: function () {
        let authorization=localStorage.getItem('authorization')
      let user = localStorage.getItem('user');
      user = JSON.parse(user);
      this.user = user
     console.log(user._id)
     console.log(authorization)
      
    },
    components:{
    Rendezvous,
  
    
    },
    data: () => ({
     items: [
        { src: "https://cdn.vuetifyjs.com/images/john.jpg"},
          { src: 'url('+require(`../../assets/dentaire.jpg`)  +')'},
           { src:'url('+require(`../../assets/dentaire.jpg`)  +')'},
          { src:'url('+require(`../../assets/dentaire.jpg`)  +')'},
