<script>


import Vue from 'vue'
import pageTranslator from '@nnm/page-translator'; Vue.use(pageTranslator);
import axios from 'axios';

  import {
    extend,
    ValidationObserver,
    ValidationProvider,
    setInteractionMode
} from 'vee-validate'
import {
    required,
    
} from 'vee-validate/dist/rules'


setInteractionMode('eager')

extend('required', {
    ...required,
    message: '{_field_} ne peux pas être vide',
})

  export default {
    name:"Calendrier",
     created: function () {
        let authorization=localStorage.getItem('authorization')
      let user = localStorage.getItem('user');
      user = JSON.parse(user);
      this.user = user
     console.log(user._id)
     console.log(authorization)
      
    },
    components:{

     ValidationProvider,
        ValidationObserver,
    },
    
    data: () => ({
        date: new Date().toISOString().substr(0, 10),
        heurefin: null,
        heuredebut:null,
         
        menu2: false,
        modal2: false,
        modal3:false,
           snackbar: false,
      timeout: 10000,
      loading: false,
       
        birthday:'',
        menu1: false,
       menu3: false,
      item:[],
     dialog2:false,
         menuOpen:false,
      modal: false,
      errorMessages: '',
      name: null,
      state: null,
      formHasErrors: false,
      
     items: [
        { src: 'url('+require(`../../assets/dentaire.jpg`)  +')'},
