        ],
      image: {backgroundImage:'url('+require(`../../assets/dentaire.jpg`)  +')'},
       image1: {backgroundImage:'url('+require(`../../assets/cartevisit.jpg`)  +')'},
        Calendrier:false,
        dialog: false,
      focus: '',
      type: 'month',
      typeToLabel: {
        month: 'Month',
        week: 'Week',
        day: 'Day'
      },
      selectedEvent: {},
      selectedElement: null,
      selectedOpen: false,
      events: [],
   
     
      rendezvous:[]
    }),
    load: {
    key: 'YOUR_GOOGLE_MAPS_API_KEY_GOES_HERE',
    libraries: 'places',
  },
     mounted(){
    axios.get('http://localhost:5000/user/affiche_tous_rendezvous')
    .then((response) => {
        this.rendezvous=response.data;
        console.log(this.rendezvous);
        this.updateRange()
      
    })
    .catch((error)=>{
      console.log(error);
    })
  
      this.$refs.calendar.checkChange()
    },
    methods: {
    async saved(){
    this.loading = true
          let object={
       
           date_debut:this.birthday,
           date_fin:this.birthday,
           heure_debut:this.heuredebut,
           Patient:this.user._id,
          }
          try {
              let response=  await axios.post('http://127.0.0.1:3000/user/reserver_rendezvous',object)
              if(response.status ==200){
            console.log(response)
             this.snackbar = true;
              }
          } catch (error) {
            console.log(error)
          }

          this.loading=false

       },
      viewDay ({ date }) {
        this.focus = date
        this.type = 'day'
      },
      getEventColor (event) {
        return event.color
      },
      setToday () {
        this.focus = ''
      },
     
      showEvent ({ nativeEvent, event }) {
        const open = () => {
          this.selectedEvent = event
          this.selectedElement = nativeEvent.target
          setTimeout(() => {
            this.selectedOpen = true
          }, 10)
        }
        if (this.selectedOpen) {
          this.selectedOpen = false
          setTimeout(open, 10)
        } else {
          open()
        }
        nativeEvent.stopPropagation()
      },
      updateRange() {
      let events = []
      this.rendezvous.forEach(rendezvou => {
        let couleur = ''
        let alert=''
        switch (rendezvou.description) {
          case 0:
            couleur= 'red '
            alert="consultaio terminer"
            break;
          case 1:
           couleur = 'orange'
           alert="en train de consulter"
            break;
          case 2:
           couleur = 'green'
           alert="pas en core "
            break;
            default:
            couleur="green"
             break;
         
        }
        events.push({
          name: "non diponible",
          start: rendezvou.date_debut,
          end: rendezvou.date_fin,
          color: couleur,
          alert:alert
       
        })
                this.$forceUpdate()
      });
      this.events = events
      console.log(events)
    },
   
   
    updatevent() {
      switch (this.selectedEvent.description) {
          case 0:
            this.selectedEvent.color = 'red'
            break;
          case 1:
            this.selectedEvent.color = 'orange'
            break;
          case 2:
            this.selectedEvent.color = 'blue'
            break;
            default:
           this.selectedEvent.color='red'
          break;
         
        }
        this.newtick = ''
        this.selectedOpen = false
    },
    changestate() {
      this.selectedEvent.color = 'blue'
    }
  },
 
    
  }
</script>
